import React, { FC } from "react";

import { uuid } from "../utils";

import Card from "./Card";
import { VerticalCardProps } from "./Card.types";
import { StyledVerticalCards } from "./SegmentedVerticalCards.styles";


const SegmentedVerticalCards: FC<VerticalCardProps> = ({
	ariaLabel,
	dataQA,
	className,
	mt,
	mb,
	ml,
	mr,
	m,
	cards,
}) => (
	<StyledVerticalCards
		aria-label={ariaLabel}
		data-qa={dataQA}
		className={className}
		mt={mt}
		mb={mb}
		mr={mr}
		ml={ml}
		m={m}
	>
		{cards.map((props) => (
			<Card key={uuid()} dataQA={props.dataQA} ariaLabel={props.ariaLabel} isInverted={false}>
				{props.children}
			</Card>
		))}
	</StyledVerticalCards>
);

SegmentedVerticalCards.displayName = "SegmentedVerticalCards";
export default SegmentedVerticalCards;
